.about-sideimg {
  width: 100%;
  height: 35rem;
  border-radius: 70px;
  margin-left: -10rem;
}
*{
  padding: 0;
  margin: 0;
}

.about-page {
  background-image: url("../../Images/About/About-bg.jpg");
  background-size: 100%;
  background-repeat: no-repeat;

  position: relative;
  top: 2px;

}

.aboutbanner-title2 {
  color: #fc4c4e;
  font-weight: bold;
}

.aboutbanner-image {
 
 
  box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;

  border-radius: 50%;
}
.button-33 {
  background: linear-gradient(
    350deg,
    rgb(38, 48, 51) 0%,
    rgb(247, 48, 30) 100%
  );
  border-radius: 100px;
  box-shadow: rgba(180, 44, 187, 0.2) 0 -25px 18px -14px inset,
    rgba(175, 44, 187, 0.15) 0 1px 2px, rgba(147, 14, 138, 0.15) 0 2px 4px,
    rgba(165, 12, 173, 0.15) 0 4px 8px, rgba(166, 14, 180, 0.15) 0 8px 16px,
    rgba(128, 11, 152, 0.15) 0 16px 32px;
  color: rgb(11, 11, 11);
  cursor: pointer;
  display: inline-block;
  font-family: CerebriSans-Regular, -apple-system, system-ui, Roboto, sans-serif;
  padding: 0.7rem;
  text-align: center;
  text-decoration: none;
  transition: all 250ms;
  border: 0;
  font-size: 16px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-33:hover {
  box-shadow: rgba(180, 44, 187, 0.2) 0 -25px 18px -14px inset,
    rgba(175, 44, 187, 0.15) 0 1px 2px, rgba(147, 14, 138, 0.15) 0 2px 4px,
    rgba(165, 12, 173, 0.15) 0 4px 8px, rgba(166, 14, 180, 0.15) 0 8px 16px,
    rgba(128, 11, 152, 0.15) 0 16px 32px;
  transform: scale(1.05) rotate(-1deg);
}

a {
  text-decoration: none;
  color: black;
}
.banner-foot {
  padding: 1rem 0rem 2rem 0rem;
}
.banner-foot-txt {

  font-family: "EB Garamond", serif;
  font-weight: bold;
}
.testmonials {
  background: linear-gradient(182deg, rgb(251, 251, 255) 0%, #f1f1ef 100%);
  padding: 1rem 2rem;
  border-radius: 7px;
}
.top-selling ::after {
  content: "";
  height: 2px;
  width: 15rem;
  border: 2px solid black;
}
